.light {
  padding-bottom:  85px;

  &-subtitle {
    margin: 15px auto 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 940px;
  }
  &-image {
    max-width: 1200px;
    width: 100%;
    margin: 61px auto 0;
  }
}
@media (max-width: 1250px) {
  .light {
    &-image {
      max-width: 825px !important;
      margin: 61px auto 0 !important;
    }
  }
}
@media (max-width: 1000px) {
  .light {
    &-subtitle {
      margin: 15px auto 22px;
      max-width: 700px;
      line-height: 40px;
    }
    &-image {
      max-width: 650px !important;
      margin: 61px 43px 0 auto !important;
    }
  }
}
@media (max-width: 600px) {
  .light {
    padding-bottom: 100px;
  }
}