.more {
  padding-bottom:  69px;
  &-subtitle {
    max-width: 900px;
    width: 100%;
    margin: 15px auto 22px;
    line-height: 46px;
  }
  &-text {
    max-width: 750px;
  }
  &-note {
    margin: 69px auto 0;
  }
  &-image {
    max-width: 1340px;
    width: 100%;
    margin: 48px auto 0;
  }
}
@media (max-width: 600px) {
  .more {
    padding-bottom:  100px;
  }
}