.technologies {
  padding-bottom:  155px;
  &-subtitle {
    max-width: 740px;
    width: 100%;
    margin: 15px auto 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 840px;
  }
  &-image {
    margin: 0 auto 90px;
  }
  &-wrapper {
    display: flex;
    flex-direction: column;
  }
  &-box {
    display: flex;
    justify-content: space-between;
    max-width: 902px;
    margin: 0 auto 67px;
    width: 100%;
    &-image {
      max-width: 425px;
    }
  }
  &-item {
    max-width: 445px;
    margin-top: 24px;
    width: 100%;
    &-title {
      font-family: "Gotham";
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      color: #FFFFFF;
    }
    &-subtitle {
      font-family: "Gotham";
      font-style: normal;
      font-weight: normal;
      margin: 11px 0 15px;
      font-size: 20px;
      line-height: 14px;
      text-transform: uppercase;
      color: #FFFFFF;
    }
    &-text {
      font-family: "Gotham";
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: #FFFFFF;
    }
  }
}
@media (max-width: 1250px) {
  .technologies {
    padding-bottom:  71px;
    &-item {
      margin-top: 10px;
      &-title {
        font-size: 24px;
      }
      &-subtitle {
        font-size: 24px;
        line-height: 120%;
      }
      &-text {
        font-size: 18px;
        line-height: 130%;
      }
    }
  }
}
@media (max-width: 1000px) {
  .technologies {
    &-box {
      max-width: 700px;
      &-image {
        max-width: 340px;
        overflow: hidden;
        img {
          height: auto;
        }
      }
    }
    &-item {
      max-width: 340px;
      margin-top: 0;
      &-title {
        font-size: 34px;
        line-height: 130%;
        overflow: hidden;
      }
      &-subtitle {
        font-size: 30px;
        line-height: 120%;
      }
      &-text {
        font-size: 24px;
        line-height: 130%;
      }
    }
  }
}
@media (max-width: 750px) {
  .technologies {
    padding-bottom:  100px;
    &-item {
      &-subtitle {
        font-size: 24px;
      }
    }
    &-box {
      flex-direction: column;
      padding: 0 20px;
      &-image {
        max-width: 100%;
        margin-bottom: 25px;
      }
    }
    &-box:last-of-type {
      flex-direction: column-reverse;
      padding: 0 20px;
    }
  }
}