* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
@import "font";
#LG_DG-TV-OLED {
  width: 100%;
  height: 100%;
  background-color: #000000;
  font-family: "Gotham";
  font-weight: normal;
  font-style: normal;

  @import "normalize";
  @import "swiper";
  @import "sections/hero";
  @import "sections/slide";
  @import "sections/light";
  @import "sections/accuracy";
  @import "sections/technologies";
  @import "sections/dolby";
  @import "sections/webos";
  @import "sections/control";
  @import "sections/comfort";
  @import "sections/gaming";
  @import "sections/set";
  @import "sections/lgc148";
  @import "sections/design";
  @import "sections/more";
  @import "sections/evo";
  @import "sections/c1";
  @import "sections/b1";
  @import "sections/a1";
  //@import "sections/media";

  body {
    width: 100%;
    height: 100%;
    padding: 0;
    overflow-x: hidden;
    margin: 0;
  }
  .image {
    max-width: 1200px;
    width: 100%;
  }
  .note {
    font-family: "Gotham";
    font-style: normal;
    overflow: hidden;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #FFFFFF;
    max-width: 902px;
    opacity: 0.5;
    width: 100%;
  }
  .text {
    font-family: "Gotham";
    font-style: normal;
    overflow: hidden;
    font-weight: normal;
    margin: 0 auto;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    width: 100%;
  }
  .title {
    font-family: "Gotham";
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 46px;
    overflow: hidden;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .small-title {
    font-family: "Gotham";
    font-style: normal;
    font-weight: bold;
    overflow: hidden;
    font-size: 24px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .subtitle {
    font-family: "Gotham";
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    text-align: center;
    overflow: hidden;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  @media (max-width: 1250px) {
    .image {
      max-width: 825px;
      height: auto;
    }
    .note {
      font-size: 16px;
      line-height: 130%;
    }
    .small-title {
      font-size: 32px;
      line-height: 130%;
    }
    .subtitle {
      font-size: 45px;
      line-height: 120%;
    }
    .text {
      font-size: 30px;
      line-height: 110%;
    }
  }
  @media (max-width: 1000px) {
    .image {
      max-width: 650px;
      height: auto;
    }
    .note {
      max-width: 650px;
      font-size: 14px;
    }
    .small-title {
      font-size: 24px;
    }
    .subtitle {
      font-size: 32px;
    }
    .text {
      font-size: 24px;
      max-width: 700px;
      line-height: 130%;
    }
  }
}

@media (max-width: 600px) {
  .image {
    max-width: 100% !important;
    height: auto;
    padding: 0 30px;
  }
  .note {
    max-width: 650px;
    padding: 0 20px;
    font-size: 12px;
  }
  .title {
    padding: 0 25px;
  }
  .small-title {
    font-size: 14px !important;
    padding: 0 20px;
  }
  .subtitle {
    font-size: 24px !important;
    padding: 0 20px;
  }
  .text {
    font-size: 16px !important;
    padding: 0 20px;
    line-height: 130%;
  }
}