.comfort {
  padding-bottom:  80px;
  &-subtitle {
    margin: 28px 0 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 880px;
  }
  &-note {
    margin: 30px auto 0;
    span {
      display: inline-block;
    }
  }
  &-image {
    margin: 8px auto 0;
  }
}
@media (max-width: 600px) {
  .comfort {
    padding-bottom:  100px;
  }
}