.slide {
  &-wrapper {
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    max-width: 250px;
    min-height: 385px;
    padding: 60px 20px;
  }
  &-image {
    margin-bottom: 35px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &-text {
    font-family: "Gotham";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    margin-bottom: 35px;
  }
  &-price {
    display: flex;
    flex-direction: column;
    &--crossed {
      display: inline-block;
      margin-bottom: 10px;
      font-family: "Gotham";
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 14px;
      color: #000000;
      text-decoration: line-through;
    }
    &--current {
      display: inline-block;
      font-family: "Gotham";
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 18px;
      color: #000000;
    }
  }
}

@media (max-width: 600px) {
  .slide {
    &-text {
      font-size: 15px;
    }
  }
}