.control {
  padding-bottom:  80px;
  &-subtitle {
    margin: 28px 0 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 880px;
  }
  &-note {
    margin: 20px auto 0;
  }
  &-image {
    margin: 39px auto 0;
  }
}
@media (max-width: 600px) {
  .control {
    padding-bottom:  100px;
  }
}