.gaming {
  padding-bottom:  80px;
  &-subtitle {
    margin: 28px 0 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 912px;
  }
  &-note {
    margin: 26px auto 0;
    display: flex;
    flex-direction: column;
    span {
      display: inline-block;
    }
  }
  &-image {
    margin: 25px auto 0;
  }
}
@media (max-width: 600px) {
  .gaming {
    padding-bottom:  100px;
  }
}