.hero {
  padding-bottom:  74px;
  &-inner {
    background-image: url("../../images/sections/hero.png");
    background-position: center;
    background-size: 100% 100%;
    width: 1200px;
    height: 742.13px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }
  &-title {
    font-family: "Gotham";
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 35px;
    max-width: 1000px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 46% auto 30px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    &--normal {
      margin-top: 5px;
      font-weight: normal;
    }
  }
  &-subtitle {
    font-family: "Gotham";
    font-style: normal;
    font-weight: normal;
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
    font-size: 11px;
    line-height: 13px;
    color: #FFFFFF;
    opacity: 0.5;
  }
  .hero-btn {
    border-radius: 7px;
    border: 1px solid #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 349px;
    margin: 86px auto;
    text-decoration: none;
    height: 50px;
    span {
      font-family: "Gotham";
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 29px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF;
    }
  }
}
@media (max-width: 1250px) {
  .hero {
    &-inner {
      width: 100%;
      height: auto;
    }
    &-subtitle {
      max-width: 770px;
    }
  }
}
@media (max-width: 1000px) {
  .hero {
    &-title {
      font-size: 32px;
      margin: 48% auto 30px;
    }
    &-subtitle {
      max-width: 675px;
    }
    &-btn {
      margin: 43px auto 0;
    }
  }
}
@media (max-width: 600px) {
  .hero {
    padding-bottom: 40px;
    &-inner {
      height: 450px;
      padding: 0 20px ;
      justify-content: flex-end;
    }
    &-title {
      font-size: 22px;
      line-height: 21px;
      margin: 0 auto 5px;
    }
    &-subtitle {
      font-size: 7px;
    }
    &-btn {
      width: 250px !important;
      margin: 43px auto !important;
      span {
        font-size: 18px !important;
      }
    }
  }
}

@media (max-width: 380px) {
  .hero {
    &-title {
      font-size: 21px;
      &--normal {
        font-size: 16px;
      }
    }
    &-inner {
      height: 370px;
    }
  }
}