.accuracy {
  padding-bottom:  105px;
  &-note {
    display: flex;
    flex-direction: column;
    max-width: 900px;
    margin: 19px auto 0;
  }
  &-subtitle {
    margin: 15px auto 22px;
    line-height: 40px;
  }
  &-text {
    max-width: 940px;
  }
  &-image {
    margin: 61px auto 0;
  }
}
@media (max-width: 600px) {
  .accuracy {
    padding-bottom:  100px;
  }
}