.set {
  padding-bottom:  68px;
  &-wrapper {
    display: flex;
    max-width: 900px;
    width: 100%;
    justify-content: space-between;
    margin: 0 auto;
  }
  &-column {
    display: flex;
    margin-top: 22px;
  }
  &-text {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    span {
      display: inline-block;
      font-family: "Gotham";
      font-style: normal;
      font-weight: normal;
      font-size: 10px;
      margin-bottom: 23px;
      line-height: 12px;
      color: #FFFFFF;
    }
  }
  &-subtitle {
    margin: 18px 0 33px;
    line-height: 40px;
  }
  &-image {
    max-height: 304px;
  }
}
@media (max-width: 1250px) {
  .set {
    &-text {
      margin-top: 33px;
    }
  }
}
@media (max-width: 1000px) {
  .set {
    &-wrapper {
      max-width: 650px;
    }
    &-image {
      max-width: 89px;
      overflow: hidden;
    }
    &-text {
      span {
        font-size: 9px;
        line-height: 11px;
        margin-bottom: 9px;
        width: 240px !important;
      }
    }
  }
}
@media (max-width: 600px) {
  .set {
    padding-bottom:  100px;
    &-image {
     display: none;
    }
    &-wrapper {
      padding: 0 20px;
      flex-direction: column;
    }
    &-text {
      margin-top: 24px;
      span {
        width: 100% !important;
        font-size: 14px;
        line-height: 15px;
      }
    }
  }
}